import React,{Component} from 'react';
import {Row, Breadcrumb,BreadcrumbItem,Button,Label,Col} from "reactstrap";
import {Link} from "react-router-dom";
import {Control,Form,Errors} from 'react-redux-form';


const required=(val)=>val && val.length;
const maxLength=(len)=>(val)=>!(val)||(val.length<=len)
const minLength = (len) => (val) => val && (val.length >= len);
const isNumber = (val) => !isNaN(Number(val));
const validEmail = (val) => /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(val);
class Contact extends Component {
    constructor(props) {
        super(props);

        this.handleSubmit=this.handleSubmit.bind(this);
    }

    handleSubmit(values){
        console.log("Current state is "+JSON.stringify(values));
        alert("Current state is "+JSON.stringify(values));
        this.props.postFeedback(values.firstname,values.lastname,values.telnum,values.email,values.agree,values.contactType,values.message);
        this.props.resetFeedbackForm();
    }

    validate(firstname,lastname,telnum,email){
        const errors={
            firstname:'',
            lastname:'',
            telnum:'',
            email:'',
        };
        if (this.state.touched.firstname && firstname.length<3){
            errors.firstname='First name should be > 3 chars';}
        else if (this.state.touched.firstname && firstname.length>10){

            errors.firstname='First name should be <= 10 chars';}

        if (this.state.touched.lastname && lastname.length<3){
            errors.firstname='First name should be > 3 chars';}
        else if (this.state.touched.lastname && lastname.length>10){

            errors.firstname='First name should be <= 10 chars';}

        const reg= /^\d+$/;
        if (this.state.touched.telnum && !reg.test(telnum)){
            errors.telnum='Tel. Number should contain only digits';
       }
        if (this.state.touched.email && email.split('').filter(x => x==='@').length!==1){
            errors.email='Email should contain a @ sign'}

        return errors;
}
    render(){
        return(
        <div className="container">
            <div className="row">
                <Breadcrumb>
                    <BreadcrumbItem><Link to='/home'>Home</Link></BreadcrumbItem>
                    <BreadcrumbItem active>Menu</BreadcrumbItem>
                </Breadcrumb>
                <div className="col-12">
                    <h3>Contact us</h3>
                </div>
            </div>
            <div className="row row-content">

                <div className="col-12">
                <h3>Location Information</h3>
                </div>
                <div className="col-12 col-sm-4 offset-sm-1">
                        <h5>Our Address</h5>
                        <address>
                        121, Clear Water Bay Road<br />
                        Clear Water Bay, Kowloon<br />
                        HONG KONG<br />
                        <i className="fa fa-phone"></i>: +852 1234 5678<br />
                        <i className="fa fa-fax"></i>: +852 8765 4321<br />
                        <i className="fa fa-envelope"></i>: <a href="mailto:confusion@food.net">confusion@food.net</a>
                        </address>
                </div>
                <div className="col-12 col-sm-6 offset-sm-1">
                    <h5>Map of our Location</h5>
                </div>
                <div className="col-12 col-sm-11 offset-sm-1">
                    <div className="btn-group" role="group">
                        <a role="button" className="btn btn-primary" href="tel:+85212345678"><i className="fa fa-phone"></i> Call</a>
                        <a role="button" className="btn btn-info"><i className="fa fa-skype"></i> Skype</a>
                        <a role="button" className="btn btn-success" href="mailto:confusion@food.net"><i className="fa fa-envelope-o"></i> Email</a>
                    </div>
                </div>
            </div>
            <div className="row row-content">
                <div className="col-12">
                    <h3>Send us your feedback</h3>
                </div>
                <div className="col-12 col-md-9">
                    <Form model="feedback" onSubmit={(values)=>this.handleSubmit(values)}>
                        <Row className="form-group">
                            <Label htmlFor="firstname" md={2}>First Name</Label>
                            <Col md={10}>
                                <Control.text model=".firstname" id="firstname" name="firstname"
                                       placeholder="first name"
                                              className="form-control"
                                              validators={{
                                                  required,minLength:minLength(3),maxLength:maxLength(15)
                                              }}/>
                                  <Errors className="text-danger" model=".firstname"
                                  show="touched"
                                  messages={{required:'Required',
                                  minLength:"Must be greater than 2 chars",
                                  maxLength:"Must be 15 chars max"}}>

                                  </Errors>
                            </Col>
                        </Row>
                         <Row className="form-group">
                            <Label htmlFor="lastname" md={2}>Last Name</Label>
                            <Col md={10}>
                                <Control.text model=".lastname" className="form-control" id="lastname" name="lastname"
                                       placeholder="last name"
                                        validators={{
                                                  required,minLength:minLength(3),maxLength:maxLength(15)
                                              }}/>
                                  <Errors className="text-danger" model=".lastname"
                                  show="touched"
                                  messages={{required:'Required',
                                  minLength:"Must be greater than 2 chars",
                                  maxLength:"Must be 15 chars max"}}>

                                  </Errors>
                            </Col>
                        </Row>
                         <Row className="form-group">
                            <Label htmlFor="telnum" md={2}>Contact tel.</Label>
                            <Col md={10}>
                                <Control.text model=".telnum"
                                              className="form-control" type="tel" id="telnum" name="telnum"
                                       placeholder="telnum"
                                       validators={{
                                                  required,minLength:minLength(3),maxLength:maxLength(15), isNumber
                                              }}/>
                                  <Errors className="text-danger" model=".telnum"
                                  show="touched"
                                  messages={{required:'Required',
                                  minLength:"Must be greater than 2 chars",
                                  maxLength:"Must be 15 chars max",
                                  isNumber:"Must be only numbers"}}>

                                  </Errors>
                            </Col>
                        </Row>
                        <Row className="form-group">
                            <Label htmlFor="email" md={2}>Email</Label>
                            <Col md={10}>
                                <Control.text model=".email" className="form-control" type="email" id="email" name="email"
                                       placeholder="email"
                                              validators={{
                                                  required,validEmail
                                              }}/>
                                  <Errors className="text-danger" model=".firstname"
                                  show="touched"
                                  messages={{required:'Required',
                                  validEmail:'invalid email adress'
                                  }}>

                                  </Errors>
                            </Col>
                        </Row>
                         <Row className="form-group">
                            <Col md={{size:6,offset:2}}>
                                <div className="form-check">
                                    <Label check>
                                        <Control.checkbox model=".agree"  name="agree"
                                                          className="form-check-input"
                                                />{' '}
                                        <strong>May we contact you?</strong>
                                    </Label>
                                </div>
                            </Col>
                             <Col md={{size:3,offset:1}}>
                                <Control.select model=".contactType" className="form-control" name="contactType"
                                        >
                                    <option>Tel.</option>
                                    <option>Email</option>
                                </Control.select>
                             </Col>
                        </Row>
                        <Row className="form-group">
                            <Label htmlFor="message" md={2}>Your feedback</Label>
                            <Col md={10}>
                                <Control.textarea model=".message" id="message" name="message" rows="12"
                                       placeholder="your feedbacks" className="form-control"/>
                            </Col>
                        </Row>
                        <Row className="form-group">
                            <Col md={{size:10,offset:2}}>
                                <Button type="submit" color="primary">
                                    Send Feedback
                                </Button>
                            </Col>
                        </Row>

                    </Form>
                </div>
            </div>
        </div>
    );
    }
}

export default Contact;